import opg from 'ts/opg.ts';
import Panel from 'ts/ui/Panel.ts';
import Table from 'ts/ui/Table.ts';
import {Combo} from 'ts/ui/Combo';
import {Languages} from 'ts/util/Languages';
import {store} from '../../../ts/util/store';
import {url} from '../../../ts/util/utils';
import {Cache} from 'ts/util/store';
import ViewNewsDetail from "./ViewNewsDetail";

let cache = Cache.getInstance();

console.log('Languages:', Languages);


opg.api({
	list: 'Information/QueryAllEnableInformations?type=0'
});

const lpg = Languages.package;

const moduleName = lpg.news;




let tb: Table = opg('#tb').table({
	api: opg.api.list,
	param: {informationType: 0},
	fixFooter: true,
	titleBar: {
		title: `${lpg.moduleName}`,
	},
	columns: [
		{
			text: `${lpg.newsTitle}`,
			src: 'title', align:'left',
			render: (title , i , row)=>{
				return `<a href="javascript:void(0)" data-id="${row.id}"><div class="thumb"><img src="${row.coverpicture}"></div><b class="nTitle">${title}</b> <br> ${row.digest}</a>`
			}
		},
		{
			text: lpg.publisher,
			src: 'from',
			width: 250,
		},
	],
	pagination: true,
});

tb.tbody.on('click' , 'a' , function () {
	new ViewNewsDetail($(this).data('id') , Languages.package.readDetail);
});

